import './Length.css'
const Length = (props) => {
    return ( 
        <div className='length'>
                    <div>
                        <p><strong>Break Length</strong></p>
                        <div>
                            <i className='timerIcon' onClick={props.breakDecrementHandler}>-</i>
                            {props.breakLength} 
                            <i className='timerIcon' onClick={props.breakIncrementHandler}>+</i>
                        </div>
                    </div>
                    <div>
                        <p><strong>Session Length</strong></p>
                        <div>
                            <i className='timerIcon' onClick={props.sessionDecrementHandler}>-</i> 
                            {props.sessionLength} 
                            <i className='timerIcon' onClick={props.sessionIncrementHandler}>+</i>
                        </div>
                    </div>
                </div>
     );
}
 
export default Length;