import './Calculator.css'
import { Component } from "react";

class Calculator extends Component{
   state={
       result:0,
       Arr:['']
   }
   handleClear= () =>{
       this.setState({result:0})
       this.setState({Arr:['']})
   }
   handleBackspace = ()=>{
       this.state.Arr.pop()
       this.setState({result: this.state.Arr.join('')})
  }

    handleClick =(e)=>{
        if(typeof('+')){console.log('yes its fucking string'+e.target.name)}
        this.setState({result:this.state.Arr.join('')})
        this.state.Arr.push(e.target.value)
        this.setState({result:e.target.value})    
        }
        
        handleResult = ()=>{
            try {
                let finalResult = eval(this.state.Arr.join(''))
                this.setState({result:finalResult===undefined?'error':finalResult})     
            } catch (error) {
            this.setState({result:'error'}) 
        }
    }
    render(){
        return(
            <div className='container'>
                    <div className='output'> {this.state.Arr.join('')}</div>
                    <div className='output'>{this.state.result}</div>
                <div className="keypad">
                    <button className='calbtn'   onClick={this.handleClear} id='clear'>Clear</button>
                    <button className='calbtn'   onClick={this.handleBackspace} id='backspace' >C</button>
                    <button className='calbtn' value='/' name='/' onClick={this.handleClick}>&divide;</button>
                    <button className='calbtn' value='7' onClick={this.handleClick}>7</button>
                    <button className='calbtn' value='8' onClick={this.handleClick}>8</button>
                    <button className='calbtn' value='9' onClick={this.handleClick}>9</button>
                    <button className='calbtn' value='*' name='*' onClick={this.handleClick}>&times;</button>
                    <button className='calbtn' value='4' onClick={this.handleClick}>4</button>
                    <button className='calbtn' value='5' onClick={this.handleClick}>5</button>
                    <button className='calbtn' value='6' onClick={this.handleClick}>6</button>
                    <button className='calbtn' value='-' name='-' onClick={this.handleClick}>&ndash;</button>
                    <button className='calbtn' value='1' onClick={this.handleClick}>1</button>
                    <button className='calbtn' value='2' onClick={this.handleClick}>2</button>
                    <button className='calbtn' value='3' onClick={this.handleClick}>3</button>
                    <button className='calbtn' value='+' name='+' onClick={this.handleClick}>+</button>
                    <button className='calbtn' value='0' onClick={this.handleClick}>0</button>
                    <button className='calbtn' value='.' onClick={this.handleClick}>.</button>
                    <button className='calbtn' value='=' onClick={this.handleResult} id='result'>=</button>
                </div>
            </div>
        )
    }
}

export default  Calculator;