import { useState } from 'react';
import './Alarm.css'
import Length from './Length/Length';
import Session from './Session/Session';
let timer ;
let minutes;
let seconds;
let style;
let beeb = 'https://raw.githubusercontent.com/freeCodeCamp/cdn/master/build/testable-projects-fcc/audio/BeepSound.wav'
const Alarm =()=> {
    const[breakLength,setBreakLength] = useState(5)
    const[sessionLength,setSessionLength] = useState(25)
    const[sessionMin,setSessionMin] = useState(25)
    const[sessionSec,setSessionSec] = useState(0)
    const[play,setPlay] = useState(true)
    const[breakTime,setBreakTime] = useState(false)

    const breakIncrementHandler = ()=>{
        setBreakLength(breakLength+1)
        console.log(breakTime);
    }
    const breakDecrementHandler = ()=>{
        if(breakLength>1){
            setBreakLength(breakLength-1)
        }
    }
    const sessionIncrementHandler = ()=>{
        setSessionLength(sessionLength+1)
        setSessionMin(sessionMin+1)
    }
    const sessionDecrementHandler = ()=>{
        if(sessionLength>1){
        setSessionLength(sessionLength-1)
        setSessionMin(sessionMin-1)
        }
    }


    let nowSeconds =  sessionMin*60+sessionSec ;
    function playSessionHandler (){
        if(play){
            timer = setInterval(() => {
                    if(nowSeconds>0){
                        
                            nowSeconds-- ;
                            minutes = Math.floor(nowSeconds/60);
                            seconds = Math.floor(nowSeconds%60);
                            setSessionSec(seconds) 
                            setSessionMin(minutes)
                            if(nowSeconds===0){
                                nowSeconds=breakLength*60
                                setBreakTime(true)
                                document.getElementById('beeb').play();
                            }
                        }
                    }, 1000 );
                    setPlay(false)
                }
        else{
            clearInterval(timer)
            setPlay(true)
        } 
    }

    
    function restartHandler(){
        setBreakLength(5)
        setSessionLength(25)
        setSessionMin(25)
        setSessionSec(0)
        setPlay(true)
        clearInterval(timer)
        setBreakTime(false)
        
    }

        return ( 
            <div className='alarm'>
                <h2>25 + 5 Clock</h2>
                <Length
                breakDecrementHandler={breakDecrementHandler}
                breakIncrementHandler={breakIncrementHandler}
                sessionDecrementHandler={sessionDecrementHandler}
                sessionIncrementHandler={sessionIncrementHandler}
                breakLength={breakLength}
                sessionLength={sessionLength}
                />
                <Session
                sessionMin={sessionMin}
                sessionSec={sessionSec}
                breakTime={breakTime}
                
                /> 
                <audio id='beeb' src={beeb} preload="none"  type="audio/mpeg"></audio>
                <div><i className='timerIcon'  onClick={()=>{
                    playSessionHandler();
                 }} >{play?'Start':'Stop'}</i>
                <i style={style} className='timerIcon' onClick={()=>{
                    restartHandler();
                }
                }>Restart</i></div>
            </div>
     );
    
}
 
export default Alarm;