import './Quote.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faTumblr,
    faTwitter
  } from '@fortawesome/free-brands-svg-icons';
 import { useState } from 'react';
import Loading from '../Loading/Loading';
 
const Quote = (props) => {
const[randomColor,setRandomColor] = useState('rgb(69, 96, 219)')
// document.body.style.backgroundColor = randomColor;
const[x,setX] = useState('220px')
const[y,setY] = useState('350px')
// console.log(x,y);
    //         Base 2: Binary, 2 numbers: [0, 1]
    // Base 8: Octal, 8 numbers: [0, 7]
    // Base 10: Decimal, 10 numbers: [0, 9]
    // Base 16: Hexadecimal, 16 symbols: [0, 9] and [A, F]
    // let styleBg = {backgroundColor:'rgb(69, 96, 219)'}
    // let styleColor; // {color:'rgb(69, 96, 219)'}
    
    let setBg = () => {
        setRandomColor ("#"+Math.floor(Math.random()*16777215).toString(16)) 
    }
    
    window.onmouseover = (e)=> {
        // setTimeout(() => {
        setX(e.clientX+15+'px') 
        setY(e.clientY+15+'px') 
        //  console.log(x,y);
    // }, 1000);
};

    return ( 
        <div className="quoteApp" style={{backgroundColor:randomColor}}>
                {/* {console.log(props.isPending)} */}
                <p className='nameRumiani'>Random Quote</p>
                <div  className="main">
                    
                    <div className='quote'>
                    {props.isPending? <Loading bgcolor={randomColor}/>:
                            <div>
                            <p  style={{color:randomColor}} title="regular" className='textQuote'> <strong>&ldquo;</strong>{props.quote.text}</p>
                            <h4 style={{color:randomColor}}>- {props.quote.author}</h4>
                            </div>
                        }
                        </div>    
                    {/* <br/> */}
                    <div className='btns'>
                        <a style={{backgroundColor:randomColor}} href={"https://twitter.com/intent/tweet?hashtags=quotes&related=rumiani&text="+props.quote.text} target='_blank' rel="noreferrer"><FontAwesomeIcon icon={faTwitter}/></a>
                        <span style={{top:y,left:x}} className='tooltipTwitter'>Tweet it</span>
                        <a style={{backgroundColor:randomColor}} href={"https://www.tumblr.com/widgets/share/tool?posttype=quote&tags=quotes,rumiani&caption="+props.quote.author+"&content="+props.quote.text+"&canonicalUrl=https%3A%2F%2Fwww.tumblr.com%2Fbuttons&shareSource=tumblr_share_button"} target='_blank' rel="noreferrer"><FontAwesomeIcon icon={faTumblr} /></a>
                        <span style={{top:y,left:x}} className='tooltipSpanTumblr'>Post it on tumblr</span>

                        <button style={{backgroundColor:randomColor}} onClick={() => {props.getData(); setBg();}} >Next</button>
                    </div>
                </div>
            <p className='nameRumiani'>Rumiani</p>
        </div>
     );
}
 
export default Quote;