import './ExpandColaps.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab  } from '@fortawesome/free-brands-svg-icons'
import {faExpandArrowsAlt,faCompressArrowsAlt , faArrowRight} from '@fortawesome/free-solid-svg-icons'
import { useState } from 'react';

library.add(faArrowRight,fab ,faExpandArrowsAlt,faCompressArrowsAlt)

const ExpandColaps = (props) => {
    const[ExpandColaps,setExpandColaps] = useState(false);
  const changeIcon = () =>{
     setExpandColaps(!ExpandColaps)
  }
    return ( 
        <div className='ExpandColaps' onClick={() => {changeIcon();props.changeSize(ExpandColaps)}}>
              {ExpandColaps?
              <FontAwesomeIcon className='icon'  icon={faCompressArrowsAlt}/>:
              <FontAwesomeIcon className='icon'  icon={faExpandArrowsAlt}/> }
             </div>
     );
}
 
export default ExpandColaps;