 import './Keyboard.css'
//  let audio = new Audio('https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3')
const Keyboard =(props)=> {

  const play = (name)=>{
      let playPromise =  document.getElementById(name);
      // audio.play()
      if(props.power  ){
        playPromise.currentTime =0;
        playPromise.play()     
      }
      playPromise.volume=props.volumeValue;
    }
    document.addEventListener('keydown',(event)=>{
      // console.log(event);
      // console.log(props.power);
      if(props.power){
        for(let i in props.keyCodes){
                if(event.keyCode===props.keyCodes[i] ){
                  let keyPlay = document.getElementById(props.names[i])
                  props.showName(i);
                  keyPlay.currentTime =0;
                  keyPlay.play();
                  document.getElementById(i).classList.add("playing")
                  setTimeout(() => {
                    document.getElementById(i).classList.remove('playing')
                  }, 200);
    
        }  
      } 
    }}, {once: true});  
 
         return ( 
        <div className='keyboard'>
            {   props.names.map((name,i)=>
                <div className='eachBtn' id={i} key={i} 
                onClick={()=>{play(name);props.showName(i);}}> 
                                        
                  <audio className='audio' key={i} id={name} src={props.srcS[i]} preload="none"  type="audio/mpeg" ></audio>
                  {name}
                </div>
             )
             }
            
        </div>
     )
    
}
 
export default Keyboard;