import { useState } from "react";
import './Editor.css'
import ExpandColaps from "../ExpandColaps/ExpandColaps";

const Editor  = (props) => {
    const [style,setStyle] = useState(null)
 const changeSize = (ExpandColaps) =>{
    document.querySelector('.preview').style.display = ExpandColaps?'block':'none';

    ExpandColaps? setStyle():
     setStyle({width: '80vw',height:'70vh', zIndex:'2'});
 }
//   '400px',zIndex:'1'
    return ( 
        <div  className='Editor' style={style}>
            <div className='toolbar'> Editor
                <ExpandColaps changeSize={ExpandColaps=> changeSize(ExpandColaps)} />
            </div>
            <textarea   className='input' type="text"  defaultValue={props.value} onChange={props.change}></textarea>
         </div>
     );
}
 
export default Editor ;