import './option.css';

const Option = (props) => {
    let stylePower={
        float:props.power?'right':'left'
    }
    let styleBank={
        float:props.bank?'right':'left'
    }
    return ( 
        <div className='option'>
            <div>
                <div>{props.power?'On':'Off'}</div>
                <div className='power' ><div onClick={props.powerMargin} style={stylePower}></div></div>
                <br />
                <div className='soundName'><div  >{props.inputValue}</div></div>
                <br />
                <div>volume</div>
                <div className='volume'  ><input onChange={props.volume} type="range" step="0.01" min="0" max="1" value={props.volumeValue}/></div>
                <br />
                <div>bank</div>
                <div className='bank'><div onClick={props.bankFunction} style={styleBank}></div></div>

            </div>
        </div>
     );
}
 
export default Option;