import { useEffect, useState } from "react";
import Alarm from "./Alarm/Alarm";
import './App.css'
import Calculator from "./Calculator/Calculator";
import Drum from "./Drum/Drum";
import MarkdownPreviewer from "./MarkdownPreviewer/Markdown Previewer";
import RandomQuote from "./RandomQuote/RandomQuote";
let projects = [<RandomQuote/>,<MarkdownPreviewer/>,<Drum/>,<Calculator/>,<Alarm/>]
const App = () => {
const[slideNumber,setSlideNumber] = useState(1)

const plusSlides =(n)=> {
    let newSlideNumber;
    if(slideNumber===5 && n===1){
        newSlideNumber = 1
        return setSlideNumber(newSlideNumber)
    }
    if(slideNumber===1 && n=== -1){
        newSlideNumber = 5
        return setSlideNumber(newSlideNumber)
    }
    else{
        newSlideNumber = slideNumber + n;
        setSlideNumber(newSlideNumber)
    }
  }
  useEffect(()=>{
    let dots = document.getElementsByClassName('dot')
    for(let dot of dots){
        dot.style.backgroundColor = 'gray'
    }
    dots[slideNumber-1].style.backgroundColor = 'black'
  })
  const currentSlide = (e)=> {
      setSlideNumber(+e.target.id)
}

    return(
        <div>
            <nav className='navbar'><h1>Freecodecamp React Apps</h1></nav>
            <div className="slideshow-container">
                <div className="mySlide fade">
                    <div className="numbertext">{slideNumber} / 5</div>
                <div className='eachApp'>{projects[slideNumber-1]}</div>
                </div>  
            </div>
            <button className="prev" onClick={()=>plusSlides(-1)}>&#10094;</button>
            <button className="next" onClick={()=>plusSlides(1)}>&#10095;</button>
            <div style={{textAlign:'center'}}>
                {projects.map((project,index)=><span className="dot" id={index+1} key={index} onClick={currentSlide}></span>) }
            </div>
        </div>
    )
}

export default App;