import './Session.css'
const Session = (props) => {
let style;
if(props.sessionMin<1 || (props.breakTime && props.sessionSec !==0 )){style={color:'red'}}

    
    return ( 
        <div style={style} className="session" >
                    <p><strong>{props.breakTime?'Break':'Session'}</strong></p>
                    <div>{props.sessionMin}:{props.sessionSec}</div>
                </div>
     );
}
 
export default Session;