import { Component } from "react";
import Quote from "./Quote/Quote";

class RandomQuote extends Component{
   state = {
        text:null,
        author:null,
        isPending:false
       }
 getData = ()=>{
    this.setState({isPending:true})
     let quote;
    fetch('https://type.fit/api/quotes')
        .then(res =>{
            // console.log(res)
            return res.json()
        })
        .then(data=>{
            //1643+1
            quote = data[Math.floor(Math.random()*1644)];
            this.setState({text:quote.text , author:quote.author, isPending:false})
        })
}

render(){
    window.onload =this.getData;
    return(
        <div>
        <Quote isPending={this.state.isPending} getData={this.getData} quote={this.state}></Quote>
        </div>
    )
}
}

export default RandomQuote;