import { Component } from 'react';
import './Drum.css';
import Keyboard from './Keyboard/Keyboard';
import Option from './option/option';
class Drum extends Component {
    state={
        names:['Q','W','E','A','S','D','Z','X','C'],
        keyCodes:[81,87,69,65,83,68,90,88,67],
        inputNames : ['Heater 1','Heater 2','Heater 3','Heater 4','Clap','Open HH',"Kick n' Hat",'Kick','Closed HH'],
        inputBankNames : ['Chord 1','Chord 2','Chord 3','Shaker','Open HH','Closed HH',"Punchy Kick",'Side Stick','Snare'],
        srcS:[ 
                'https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3'
            ],
        srcSBank:[
                'https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3',
                'https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3'
                ],
            power:true,
            inputValue:'Rumi Drum',
            volumeValue:'0.5',
            Bank:false
    }

    nameHandler=(i)=>{
         if(this.state.power){
            if(this.state.bank){
                this.setState({inputValue:this.state.inputBankNames[i]})
            }
            else{
                this.setState({inputValue:this.state.inputNames[i]})
            }
        }
    }
    
    volumeHnadler= (event)=>{
        this.setState({volumeValue:event.target.value})
        this.setState({inputValue:`Volume: ${Math.round(event.target.value*100)}`})

    }
    powerHndler = ()=>{
        let currentPower = this.state.power;
        this.setState({power:!currentPower})
    }
    bankHndler = ()=>{      
        let currentBank = this.state.bank;
        this.setState({bank:!currentBank})    
        console.log('bank: '+currentBank);
    }
       
    render(){
          
        return ( 
        <div className='Drum'>
            <Keyboard 
            className='Keyboard'
            names={this.state.names} 
            srcS={this.state.bank?this.state.srcSBank:this.state.srcS}
            power={this.state.power}
            volumeValue={this.state.volumeValue}
            showName={this.nameHandler}
            keyCodes={this.state.keyCodes}
             /> 

            <Option 
            className='Option'
            powerMargin={this.powerHndler} //margin the power and change the word to off and on
             bankFunction={()=>{  
                this.bankHndler()
                // this.srcBankHandler()
            }} 
            bank={this.state.bank}
            volume={this.volumeHnadler}
            volumeValue={this.state.volumeValue}
            inputValue={this.state.inputValue}
            power={this.state.power}
             /> 
        </div>
     );
    }
    
}
 
export default Drum;